.form-field {
    position: relative;
    width: 100%;
    margin-top: 2vh;
}

.form-field input {
    width: 100%;
    padding: 3vh 1vw;
    padding-right: 4vw;
    outline: none;
    border-radius: 10px;
    background-color: var(--bg-secondary);
    box-shadow: var(--dropshadow-sm);
    box-sizing: border-box;
    border: 1px solid var(--grey-300);
    color: var(--text-primary);
    font-size: 1em;
    letter-spacing: 0.05em;
    transition: border-color 0.3s ease;
}

.form-field input:focus {
    border-color: var(--primary-color);
}

.form-field .popup_text {
    position: absolute;
    left: 0;
    font-size: 1em;
    padding: 3vh 1vw;
    color: var(--text-secondary);
    letter-spacing: 0.1em;
    border-radius: 4px;
    transition: 0.25s;
    pointer-events: none;
}

.form-field input:focus ~ .popup_text,
.form-field .popup_text.has-content {
    transform: translate(15px, -10px);
    font-size: 0.75em;
    background: var(--primary-color);
    color: var(--bg-primary);
    padding: 2px 5px;
    font-weight: bold;
}

.toggle-password {
    position: absolute;
    right: 1vw;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    width: 24px;
    height: 24px;
    opacity: 0.6;
    transition: opacity 0.2s ease;
}

.toggle-password:hover {
    opacity: 1;
}

/* Hide the default password reveal button - we will use our own */
input[type="password"]::-ms-reveal,
input[type="password"]::-ms-clear {
    display: none;
}
